import { homeCard } from "@/data";

export default function HomeCard () {
  return (
    <div className="flex flex-col lg:flex-col items-start px-20 mt-10 gap-5">
      <h2 className="text-3xl font-semibold">Homes For You</h2>
      <hr className="w-full h-0.5 bg-gray-700 border-none" />
       <div className="grid lg:grid-cols-3 grid-cols-1 items-center">
        {homeCard.map((card, index) => (
          <div className="flex flex-col gap-3 border-2 border-gray-600" key={index}>
             <img src="" alt="" />
          </div>
        ))}
       </div>
    </div>
  )
}